$hide-platforms: true;
$hide-skills: true;

* { 
    padding: 0;
}

@import './button.scss';
@import './flags.scss';

$profile-image-url: 'https://d.lu.je/avatar/lachee?size=2048' !default;

.poppins { font-family: 'Poppins', sans-serif; }
.syne { font-family: 'Syne Mono', monospace; }

template { display: none !important; }
html, body {
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif; 

    position: fixed;
    width: 100%;
    height: 100%;
    overflow: none;

    $color-from:  rgb(238, 233, 174);
    $color-to:  rgb(233, 148, 187);

    background: $color-to;
    background: linear-gradient(321deg, $color-from 0%, $color-to 100%);
}

/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}

.column {
    width: 50%;
    float: left;
    height: 100%;

    &.column-right {
        width: 40%;
        float: right;
        clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);
        background-attachment: fixed;
        transition: width 0.5s ease-in-out, background-size 0.5s ease;

        &:hover, &.extend {
            width: 42%;
             /* filter: blur(1px); <- This apparently fucks up the preview-image's width ????? */

            $static-alpha: 0.8;
            $static-url: url('~images/static.gif');

            // We wish tos how the static response
            background: 
                linear-gradient(135deg, rgba(255, 255, 255, $static-alpha), rgba(255, 255, 255, $static-alpha)),  
                $static-url;
            background-size: cover;
            background-attachment: fixed;

            // We only want the peview-image to be visible while we are extended.
            .preview-image.visible { opacity: 1; }
            .preview-video.visible { opacity: .9; }
        }

        .preview-image, .preview-video {
            $fade-time: 0.75s;

            position: absolute;
            width: 100%;
            height: 100%;
            right: 0;
            z-index: -1;

            opacity: 0;
            transition: opacity $fade-time;

            &.hidden { display: none; opacity: 0; }
            &.loading {
                display: block;
                top: 0; left: 0;
                width: 100px; height: 100px;
                position: fixed;
            }
        }
        .preview-video { width:  unset; }
    }

    // &.column-left { }
}

.graph-paper {
    background: white;
    background-position: 10px 25px;
    background-size: 60px 60px;
    background-image:
        linear-gradient(to right, #e2e2e2  2px, transparent 2px),
        linear-gradient(to bottom, #e2e2e2  2px, transparent 2px);
        
}

.popout {
    box-shadow: 0px 0px black;
    transition: box-shadow 0.25s ease-out, transform 0.25s ease-out;
    .hover-box:hover:not(.dragging) > & {
        box-shadow: 10px 10px black;
        transform: translate(-10px, -10px);
    }
    .hover-box.dragging:hover > & {
        box-shadow: none;
    }
}

