#main-window {
    top: 25%;
    left: 25%;
    position: absolute;
}


.link-content { 
    margin-left: 30%; 
    margin-top: 10vh;
    .title {
        margin-top: 10pt;
        font-size: 14pt;
        font-family: 'Poppins';
        font-weight: bold;
        text-shadow: -1px 0px 0px #000000;
        text-decoration: underline wavy;
        color: #7b9eff;
    }
}

/** Special social media box */
#win-social {
    .social-navigation {
        float: left;
    }

    .social-navigation, .special-box {
        display: inline-block;
    }
    .special-box {
        
        width: 390px;
        height: 340px;
        //transform: rotate(358deg);  // << This method has bad aliasing
        transform: rotate3d(0, 0, 1, 358deg);
        
        video {
            position: relative;
            top: 10px;
        }

        /** This class displays the profile image instead of the video */
        &.profile-image {
            // Set the background
            background-image: url($profile-image-url);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;


            // Hide the video
            video { display: none; }
        }
    }
}

/**
 * Random Shapes
*/
.circle-dot-png, .square-dot-png, .triangle-dot-png  {
    background: white;
    background-size: 30px 30px;
    background-image: linear-gradient(45deg, #e9e9e9 25%, transparent 25%), linear-gradient(-45deg, #e9e9e9 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #e9e9e9 75%), linear-gradient(-45deg, transparent 75%, #e9e9e9 75%);
    background-position: 0 0, 0 15px, 15px -15px, -15px 0px;
    width: 400px;
    height: 400px;

    position: absolute;
    top: -200;
    left: -200;
    z-index: -1;
    
    border: 3px solid black;
    border-radius: 100%;

    display: none;
}
.square-dot-png { 
    border-radius: 0;
    background: rgb(131,58,180);
    background: linear-gradient(314deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 52%, rgba(252,176,69,1) 100%);
    top: -100;
    left: 100;
    width: 200px;
    height: 200px;
}
.circle-dot-sun {
    z-index: 1;
    width: 200px;
    height: 200px;
    position: absolute;
    border-radius: 100%;
    background: repeating-linear-gradient(320deg, transparent, transparent 12px, #f5ff0091 10px, #f5ff0091 20px );
    -webkit-mask-image: -webkit-linear-gradient(bottom left, #ff000000 17%, black 75%);
    bottom: -150;
    right: -250;
}
